"use client";

import { useState } from "react";
import { GlassCard } from "@/components/layout/glass-card";
import { OnboardingLayout } from "./onboarding-layout";
import { useOnboardingStore } from "@/stores/onboarding-store";

export function StepConnect() {
  const { connection, setConnection, setTestResult, testResult, nextStep } =
    useOnboardingStore();
  const [testing, setTesting] = useState(false);

  async function handleTest() {
    setTesting(true);
    setTestResult(null);
    try {
      const res = await fetch("/api/ha/test", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          baseUrl: connection.baseUrl,
          token: connection.token,
        }),
      });
      const data = await res.json();
      if (data.ok) {
        setTestResult({ ok: true });
      } else {
        setTestResult({ ok: false, error: data.error ?? "Connection failed" });
      }
    } catch (err) {
      setTestResult({
        ok: false,
        error: err instanceof Error ? err.message : "Request failed",
      });
    } finally {
      setTesting(false);
    }
  }

  async function handleSaveAndContinue() {
    if (!testResult?.ok) return;
    setTesting(true);
    try {
      const res = await fetch("/api/ha/connection", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          baseUrl: connection.baseUrl,
          token: connection.token,
        }),
      });
      const data = await res.json();
      if (data.connectionId) {
        setConnection({ connectionId: data.connectionId });
        nextStep();
      }
    } finally {
      setTesting(false);
    }
  }

  return (
    <OnboardingLayout
      step={2}
      heroTitle="Connect Home Assistant"
      heroSubtitle="Enter your instance URL and a Long-Lived Access Token. Test the connection before continuing."
      rightCards={
        <GlassCard>
          <h3 className="text-card-title font-medium mb-3">Test result</h3>
          {testResult === null && !testing && (
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Click Test connection to verify.
            </p>
          )}
          {testing && (
            <p className="text-sm text-gray-600 dark:text-gray-300">
              Testing…
            </p>
          )}
          {testResult?.ok && (
            <div className="space-y-2">
              <p className="text-sm text-green-600 dark:text-green-400 font-medium">
                Connection successful.
              </p>
              <p className="text-xs text-gray-500 dark:text-gray-400">
                You can save and continue to discovery.
              </p>
            </div>
          )}
          {testResult && !testResult.ok && (
            <div className="space-y-2">
              <p className="text-sm text-red-600 dark:text-red-400 font-medium">
                Connection failed
              </p>
              <p className="text-xs text-gray-600 dark:text-gray-300">
                {testResult.error}
              </p>
              <p className="text-xs text-gray-500 dark:text-gray-400">
                Tips: Create a new token in HA Profile, Long-Lived Access Tokens; use HTTPS or a reverse proxy if needed.
              </p>
            </div>
          )}
        </GlassCard>
      }
    >
      <div className="space-y-4 max-w-md">
        <div>
          <label htmlFor="baseUrl" className="block text-sm font-medium mb-1">
            Base URL
          </label>
          <input
            id="baseUrl"
            type="url"
            value={connection.baseUrl}
            onChange={(e) => setConnection({ baseUrl: e.target.value })}
            placeholder="http://homeassistant.local:8123"
            className="w-full rounded-lg border border-gray-300 dark:border-white/20 bg-white dark:bg-white/5 px-3 py-2 text-sm"
          />
        </div>
        <div>
          <label htmlFor="token" className="block text-sm font-medium mb-1">
            Long-Lived Access Token
          </label>
          <input
            id="token"
            type="password"
            value={connection.token}
            onChange={(e) => setConnection({ token: e.target.value })}
            placeholder="Paste your token"
            className="w-full rounded-lg border border-gray-300 dark:border-white/20 bg-white dark:bg-white/5 px-3 py-2 text-sm"
          />
        </div>
        <div className="flex gap-2">
          <button
            type="button"
            onClick={handleTest}
            disabled={testing}
            className="rounded-full bg-accent-yellow dark:bg-accent-green px-4 py-2 text-sm font-medium text-gray-900 disabled:opacity-50"
          >
            Test connection
          </button>
          {testResult?.ok && (
            <button
              type="button"
              onClick={handleSaveAndContinue}
              disabled={testing}
              className="rounded-full bg-white dark:bg-white/10 border border-gray-200 dark:border-white/20 px-4 py-2 text-sm font-medium"
            >
              Save and continue
            </button>
          )}
        </div>
      </div>
    </OnboardingLayout>
  );
}
