"use client";

import { useEffect, useState, useMemo } from "react";
import { GlassCard } from "@/components/layout/glass-card";
import { OnboardingLayout } from "./onboarding-layout";
import { useOnboardingStore } from "@/stores/onboarding-store";

const DOMAIN_FILTERS = [
  "light",
  "switch",
  "climate",
  "sensor",
  "media_player",
  "vacuum",
] as const;

function getDomain(entityId: string): string {
  return entityId.split(".")[0] ?? "";
}

export function StepDiscovery() {
  const { setEntities, entities, connectionId } = useOnboardingStore();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [search, setSearch] = useState("");
  const [domainFilter, setDomainFilter] = useState<string | null>(null);

  useEffect(() => {
    let cancelled = false;
    setLoading(true);
    setError(null);
    const url = connectionId
      ? `/api/ha/entities?connectionId=${encodeURIComponent(connectionId)}`
      : "/api/ha/entities";
    fetch(url)
      .then((r) => {
        if (!r.ok) return r.json().then((body) => ({ error: body?.error ?? "Ophalen mislukt" }));
        return r.json();
      })
      .then((data) => {
        if (cancelled) return;
        if ("error" in data) {
          setError(data.error);
          return;
        }
        if (Array.isArray(data)) setEntities(data);
      })
      .catch(() => {
        if (!cancelled) setError("Verbinding mislukt. Controleer of Home Assistant bereikbaar is.");
      })
      .finally(() => {
        if (!cancelled) setLoading(false);
      });
    return () => {
      cancelled = true;
    };
  }, [connectionId, setEntities]);

  const filtered = useMemo(() => {
    let list = entities;
    if (domainFilter) {
      list = list.filter((e) => getDomain(e.entity_id) === domainFilter);
    }
    if (search.trim()) {
      const q = search.trim().toLowerCase();
      list = list.filter(
        (e) =>
          e.entity_id.toLowerCase().includes(q) ||
          String((e.attributes as { friendly_name?: string })?.friendly_name ?? "")
            .toLowerCase()
            .includes(q)
      );
    }
    return list;
  }, [entities, domainFilter, search]);

  return (
    <OnboardingLayout
      step={3}
      heroTitle="Discover entities"
      heroSubtitle="Entities from your Home Assistant instance. Filter by domain or search to find the ones you need."
      rightCards={
        <GlassCard>
          <h3 className="text-card-title font-medium mb-2">Filters</h3>
          <p className="text-xs text-gray-500 dark:text-gray-400 mb-3">
            {entities.length} entities loaded. Use chips and search to narrow.
          </p>
          <div className="flex flex-wrap gap-1">
            <button
              type="button"
              onClick={() => setDomainFilter(null)}
              className={`rounded-full px-2.5 py-1 text-xs font-medium ${
                domainFilter === null
                  ? "bg-accent-yellow dark:bg-accent-green text-gray-900"
                  : "bg-gray-200 dark:bg-white/10 text-gray-600 dark:text-gray-300"
              }`}
            >
              All
            </button>
            {DOMAIN_FILTERS.map((d) => (
              <button
                key={d}
                type="button"
                onClick={() => setDomainFilter(d)}
                className={`rounded-full px-2.5 py-1 text-xs font-medium ${
                  domainFilter === d
                    ? "bg-accent-yellow dark:bg-accent-green text-gray-900"
                    : "bg-gray-200 dark:bg-white/10 text-gray-600 dark:text-gray-300"
                }`}
              >
                {d}
              </button>
            ))}
          </div>
        </GlassCard>
      }
    >
      <div className="space-y-4">
        <input
          type="search"
          placeholder="Search by entity_id or name..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-full max-w-md rounded-lg border border-gray-300 dark:border-white/20 bg-white dark:bg-white/5 px-3 py-2 text-sm"
        />
        {error && (
          <div className="rounded-card border border-amber-200 dark:border-amber-800 bg-amber-50 dark:bg-amber-950/30 p-4 text-sm text-amber-800 dark:text-amber-200">
            {error}
            {error.includes("onboarding") || error.includes("connection") ? (
              <p className="mt-2 text-xs">
                Ga terug naar stap 2, test de verbinding en klik op &quot;Save and continue&quot;.
              </p>
            ) : null}
          </div>
        )}
        {loading ? (
          <p className="text-sm text-gray-500">Entities ophalen…</p>
        ) : !error ? (
          <div className="rounded-card border border-gray-200 dark:border-white/10 divide-y divide-gray-200 dark:divide-white/10 max-h-[420px] overflow-auto">
            {filtered.slice(0, 100).map((e) => {
              const name =
                (e.attributes as { friendly_name?: string })?.friendly_name ??
                e.entity_id;
              return (
                <div
                  key={e.entity_id}
                  className="flex items-center justify-between px-4 py-2 text-sm"
                >
                  <span className="font-medium truncate">{name}</span>
                  <span className="text-gray-500 dark:text-gray-400 text-xs ml-2 shrink-0">
                    {e.entity_id}
                  </span>
                  <span
                    className="ml-2 shrink-0 rounded px-2 py-0.5 text-xs bg-gray-100 dark:bg-white/10"
                    title="State"
                  >
                    {e.state}
                  </span>
                </div>
              );
            })}
            {filtered.length > 100 && (
              <div className="px-4 py-2 text-xs text-gray-500">
                Eerste 100 van {filtered.length} getoond
              </div>
            )}
          </div>
        ) : null}
      </div>
    </OnboardingLayout>
  );
}
