"use client";

import { useRouter } from "next/navigation";
import { GlassCard } from "@/components/layout/glass-card";
import { OnboardingLayout } from "./onboarding-layout";
import { useOnboardingStore } from "@/stores/onboarding-store";
import { setOnboardingCompleted } from "@/lib/onboarding-completed";
import { useState } from "react";

export function StepDone() {
  const router = useRouter();
  const { template, widgets, rooms, dashboardId: storedDashboardId, dashboardName: storedDashboardName } = useOnboardingStore();
  const [exporting, setExporting] = useState(false);

  function handleExport() {
    setExporting(true);
    const config = {
      template,
      widgets,
      rooms: rooms.map((r) => ({ name: r.name, entityIds: r.entityIds })),
      exportedAt: new Date().toISOString(),
    };
    const blob = new Blob([JSON.stringify(config, null, 2)], {
      type: "application/json",
    });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = "dashboard-config.json";
    a.click();
    URL.revokeObjectURL(url);
    setExporting(false);
  }

  async function handlePublish() {
    try {
      let dashboardId = storedDashboardId;
      if (!dashboardId) {
        const res = await fetch("/api/dashboard");
        const d = await res.json();
        if (d?.id) {
          dashboardId = d.id;
        } else {
          const createRes = await fetch("/api/dashboards", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
              name: storedDashboardName || "Mijn dashboard",
              theme: "auto",
            }),
          });
          const created = await createRes.json();
          dashboardId = created?.id ?? null;
        }
      }
      if (dashboardId) {
        const layout = widgets.length
          ? widgets.map((w, i) => ({
              i: w.id,
              x: (i % 3) * 4,
              y: Math.floor(i / 3) * 2,
              w: 4,
              h: 2,
            }))
          : [];
        await fetch(`/api/dashboards/${dashboardId}`, {
          method: "PUT",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            widgets: JSON.stringify(widgets),
            layout: JSON.stringify(layout),
          }),
        });
      }
    } finally {
      setOnboardingCompleted();
      router.push("/dashboards");
    }
  }

  return (
    <OnboardingLayout
      step={8}
      heroTitle="You're all set"
      heroSubtitle="Preview your dashboard below. Publish to make it active or export the configuration."
      rightCards={
        <GlassCard>
          <h3 className="text-card-title font-medium mb-2">Summary</h3>
          <p className="text-sm text-gray-600 dark:text-gray-300">
            Template: {template}
          </p>
          <p className="text-sm text-gray-600 dark:text-gray-300">
            Widgets: {widgets.length}
          </p>
          <p className="text-sm text-gray-600 dark:text-gray-300">
            Rooms: {rooms.length}
          </p>
        </GlassCard>
      }
    >
      <div className="space-y-4 max-w-md">
        <div className="rounded-card border-2 border-dashed border-gray-300 dark:border-white/20 p-8 text-center text-gray-500 dark:text-gray-400">
          Live preview of the dashboard will appear here.
        </div>
        <div className="flex gap-2">
          <button
            type="button"
            onClick={handlePublish}
            className="rounded-full bg-accent-yellow dark:bg-accent-green px-4 py-2 text-sm font-medium text-gray-900"
          >
            Publish dashboard
          </button>
          <button
            type="button"
            onClick={handleExport}
            disabled={exporting}
            className="rounded-full bg-white dark:bg-white/10 border border-gray-200 dark:border-white/20 px-4 py-2 text-sm font-medium"
          >
            Export JSON
          </button>
        </div>
      </div>
    </OnboardingLayout>
  );
}
